@import url('https://fonts.googleapis.com/css2?family=Kumbh+Sans:wght@400;700&family=Manrope:wght@200;300;400;500;600;700;800&display=swap');

$main-font-color:#333333;
$main-font-family: 'Manrope', sans-serif;
$main-bg-color:#F7F6FF;
$main-font-color:  #333333;
$main-theme-color: linear-gradient(115.11deg, #F32C6D 10.12%, #CB1D95 100.01%);


@mixin button {
    background: linear-gradient(115.11deg, #F32C6D 10.12%, #CB1D95 100.01%);
    font-size: 1rem;
    border: none;
    outline: none;
    font-weight: 600;
    font-size: 1rem;
    color: #fff;
    padding: 14px 42px;
    border-radius: 9px;
    align-self: center;
    text-align: center;
    cursor: pointer;
    
}
    
* {
    margin: 0;
    padding: 0;
    text-decoration: none;
    box-sizing: border-box;
    font-family: $main-font-family;
    color: $main-font-color;
   
}

body {
    height: 100vh;
}

.container {
    width: 100%;
    display: flex;
    flex-direction: column;
    background-color: $main-bg-color;  
}

.padding-section{
    width: 100%;
    display: flex;
    flex-direction: column;
    padding: 5rem 6rem;
    gap: 5rem;
}

// NAVBAR
.nav-bar {
    gap: .5rem;
    width: 100%;
    display: flex;
    justify-content: space-between;
    background-color: #fff;
    box-shadow:  0px 10px 15px -3px rgba(0,0,0,0.1);
    padding: .5rem;
}  

.logo-section{
    display: flex;

  img { 
    width: 100%;
    }
}   

.menu-icon {
    padding: .6rem 0;
    border: none;
    outline: none;
    display: none;
    justify-content: center;
    align-items: center;
    background-color: #fff;
    cursor: pointer;

  img{
    height: 25px;
    width: 25px;
    }
}

.nav-links {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 1.9rem;
    background-color: #fff;

  a {
    color: $main-font-color;
    align-self: center;
    font-weight: 500;
    font-size: 1rem;
    background-color: #fff;
    padding: 5px 10px;
    border-radius: 5px;
    cursor: pointer;
    }

  a:nth-child(even):hover {
    color: #fff;
    background-color: #F32C6D;
    }

  a:nth-child(odd):hover {
    color: #fff;
    background-color: #CB1D95; 
    }
}

.nav-links-expanded {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 1.9rem;
    background-color: #fff;
    
  a {
    color: $main-font-color;
    align-self: center;
    font-weight: 500;
    font-size: 1rem;
    background-color: #fff;
    padding: 5px 10px;
    border-radius: 5px;
    cursor: pointer;
    }

  a:nth-child(even):hover {
    color: #fff;
    background-color: #F32C6D;
    }

  a:nth-child(odd):hover {
    color: #fff;
    background-color: #CB1D95;
    }
}


.button-section {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #fff   
}

.btn {
    @include button;
}


// INTRO

.intro-section {
    width: 100%;
    display: flex;
    justify-content: space-between;
    background: #F7F6FF;
    flex-wrap: wrap;
    gap: 1rem;
}  

.intro-left-side {
    min-width: 40%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 2rem;
}

.header {
    display: flex;
    flex-direction: column;

  p {
    line-height: 3.4rem;
    }

  span{
    font-weight: 600;
    font-size: 1rem;
    }
}      

.top {
    font-weight: 800;
    font-size: 2.8rem;
}

.bottom{
    font-weight: 400;
    font-size: 4rem;
}

.header-buttons {
    width: 100%;
    display: flex;
    gap: 1rem;
    flex-wrap: wrap;

  button {
    outline: none;
    border: none;
    background: #77B2D3;
    border-radius: 15rem;
    color: #fff;
    padding: .8rem 1.8rem;
    cursor: pointer;
    }
}

.intro-right-side {
    display: flex;
    min-width: 40%;

  img{
    width: 100%;
    height: 100%;
    }
}
    

// DESIGN

.design-section {
    display: flex;
    width: 100%;
    justify-content: center;
}

.design-number{
    width: 100%;
    display: flex;
    flex-direction: column;
    background: #fff;
    padding: 2.5rem 3.1rem;
    gap: 2.9rem;
    box-shadow: 0px 4px 18px rgba(0, 0, 0, 0.08);
    border-radius: 1.25rem;

}

.design-header{
    display: flex;
    background-color: #fff;

  h2{
    font-weight: 700;
    color: #333333;
    text-transform: uppercase;
    font-size: 2.3rem;
    background-color: #fff;
    }
}

.numbers-section {
    width: 100%;
    display: flex;
    background-color: #fff;
    flex-wrap: wrap;
    gap: 1rem;
}

.numbers-bottom {
    min-width: 21.9%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center; 
    background-color: #fff;
    padding-right: 3.5rem;
    border-right: 1px solid #C7C7C7;

  p{
    font-weight: 800;
    font-size: 2.5rem;
    background: -webkit-linear-gradient(115.11deg, #F32C6D 10.12%, #CB1D95 100.01%);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    }

  span{
    font-weight: 500;
    font-size: 1rem;
    background-color: #fff;
    }
}

.border-free {
   border: none;
}


// SERVICE

.service-section {
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 2rem;
}

.service-header {
    display: flex;
   
  h2{
    font-weight: 700;
    font-size: 2.3rem;
    text-transform: uppercase;
    }
}

.service-cards-section{
    flex-wrap: wrap;
    width: 100%;
    display: flex;
    gap: .6rem
  
}

.card-service-button {
    gap: 1rem;
    display: flex;
    flex-direction: column;
}

.card-service{
    width: 12.9rem;
    height: 25rem;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    box-shadow: 0px 4px 18px rgba(0, 0, 0, 0.08);
    border-radius: 15px;
    background-color: #fff;
}

.card-header {
    height: 4.4rem;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #77B2D3;
    padding: .6rem 0px;
    box-shadow: 0px 4px 18px rgba(0, 0, 0, 0.08);
    border-radius: 15px 15px 0px 0px;
    
  span{
    font-weight: 800;
    font-size: 1.2rem;
    text-align: center;
    align-self: center;
    text-transform: uppercase;
    color: #fff;
    background-color: #77B2D3;
    }
}

.card-list {
    display: flex;
    padding: 0 1rem;
    background-color: #fff;

  ul{
    height: 14rem;
    display: flex;
    flex-direction: column;
    gap: .5rem;
    list-style-type: none;
    font-weight: 400;
    font-size: 1rem;
    background-color: #fff;
    }

  li{
    display: flex;
    align-items: flex-start;
    gap: .5rem;
    background-color: #fff; 
    }
}

.card-price{
    display: flex;
    justify-content: center;
    border-top: 1px solid #E2E2E2;
    padding: .6rem 1rem;
    align-items: baseline;
    background-color: #fff;
    border-radius: 0 0 15px 15px ;
}

.span-bottom {
    display: flex;
    font-weight: 800;
    font-size: 1.8rem;
    
    background: -webkit-linear-gradient(115.11deg, #F32C6D 10.12%, #CB1D95 100.01%);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

.span-top {
    display: flex;
    font-size: 1rem;
    background-color: #fff;
}

.service-button {
    display: flex;
    justify-content: center;

  button {
    width: 12rem;   
    @include button
    }
}

// FORM

.form-section {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
}

.form-section-left{
    width: 50%;
    display: flex;
    flex-direction: column;
    gap: 1.5rem;
}

.form-header {
    display: flex;
    flex-direction: column;
    gap: 1rem;

  h1{
    font-weight: 700;
    font-size: 2rem;
    text-transform: uppercase;
    }

  span {
    font-weight: 400;
    font-size: 1rem;
    }
}

.form-page {
   width: 100%;
   display: flex;
   flex-direction: column;
   gap: 2rem;
   
  form{
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 2rem;
   }

}

.name-phone-inputs{
    width: 100%;
    display: flex;
    gap: 1.5rem;
} 

.user-name-input{
    width: 50%;
    display: flex;
    flex-direction: column;
    gap: .5rem;
    
  input{
    border: none;
    outline: none;
    width: 100%;
    padding: 1rem;
    box-shadow: 0px 4px 18px rgba(0, 0, 0, 0.08);
    border-radius: 7px;
    background-color: #fff;
    }

  input::placeholder {
    font-size: 1rem;
    }
  
  span {
    color: #F32C6D;
    font-weight: 700;
    }
}

.phone-number-input{
    width: 50%;
    display: flex;
    flex-direction: column;
    
  input{
    border: none;
    outline: none;
    width: 100%;
    padding: 1rem;
    box-shadow: 0px 4px 18px rgba(0, 0, 0, 0.08);
    border-radius: 7px;
    background-color: #fff;
    }

  input::placeholder {
    font-size: 1rem;
    }

  span {
    color: #F32C6D;
    font-weight: 700;
    }
}

.question-area{
    display: flex;
    flex-direction: column;
    
  textarea {
    width: 100%;
    border: none;
    outline: none;
    resize: none;
    box-shadow: 0px 4px 18px rgba(0, 0, 0, 0.08);
    border-radius: 7px;
    height: 7rem ;
    padding: 1rem;
    background-color: #fff;
    }

  textarea::placeholder {
    font-size: 1rem;
    }

  span {
    color: #F32C6D;
    font-weight: 700;
    }
}

.check-boxes {
    display: flex;
    flex-direction: column;
    
  p{
    font-weight: 300;
    font-size: 1rem;
    }
    
  span{
    font-weight: 700;
    color: #F32C6D;
    }
}

/* The container */
.checked {
    display: block;
    position: relative;
    padding-left: 35px;
    margin-bottom: 12px;
    font-size: 22px;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
  
    /* Hide the browser's default checkbox */
    input {
    position: absolute;
    opacity: 0;
    cursor: pointer;
    height: 0;
    width: 0;
    }

    /* Create a custom checkbox */
   .checkmark {
    cursor: pointer;
    position: absolute;
    top: 0;
    left: 0;
    height: 25px;
    width: 25px;
    background-color: #fff;
    box-shadow: 0px 4px 18px rgba(0, 0, 0, 0.08);
    border-radius: 2px;
  }

  /* On mouse-over, add a grey background color */
  .check-box:hover input ~ .checkmark {
     background-color: $main-bg-color;
  }

   /* When the checkbox is checked, add a blue background */
   input:checked ~ .checkmark {
    background: $main-theme-color;
  }
  
  /* Create the checkmark/indicator (hidden when not checked) */
  .checkmark:after {
    content: "";
    position: absolute;
    display: none;
  }
  
  /* Show the checkmark when checked */
   input:checked ~ .checkmark:after {
    display: block;
  }
  
  /* Style the checkmark/indicator */
  .checkmark:after {
    left: 9px;
    top: 5px;
    width: 5px;
    height: 10px;
    border: solid white;
    border-width: 0 3px 3px 0;
    -webkit-transform: rotate(45deg);
    -ms-transform: rotate(45deg);
    transform: rotate(45deg);
  } 

}
  
.submit-btn{
    display: flex;

  button {
    @include button
  }
}

.form-section-right {
    width: 50%;
    display: flex;

  img{
    width: 100%;
    height: 100%;
    }
}


// OUR-WORK

.our-work {
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 2rem;
    background-color: $main-bg-color;
}

.our-work-header {
    display: flex;

  h1{
    font-weight: 700;
    font-size: 2.3rem;
    text-transform: uppercase;
    }
}

.our-work-cards{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    gap: .5rem;
    border-radius: 15px; 
    background-color: $main-bg-color; 
}

.our-work-card{
    display: flex;
    flex-direction: column;
    width: 29rem; 
    box-shadow: 4px 4px 4px rgba(0, 0, 0, 0.08);
    border-radius: 15px; 
}

.our-work-logo{
    display: flex;
    flex-direction: column;
    background-color: #fff;
    padding: 1.7rem;
    border-radius: 15px 15px 0 0;

  img{
    width: 9.3rem;
    height: 7rem;
    background-color: #fff;
    }

  p{
    font-weight: 400;
    font-size: 1rem;
    background-color: #fff;
    }
}

.our-work-result{
    display: flex;
    flex-direction: column;
    gap: 1rem;
    background-color: #F3F1FE;
    padding: 1.7rem;
}

.result-top {
    display: flex;
    background-color: #F3F1FE;
    
  h4{
    font-weight: 800;
    font-size: 1.1rem;
    background-color: #F3F1FE
    }
}

.result-bottom{
    width: 100%;
    display: flex;
    justify-content: space-between;
    background-color: #F3F1FE;
}

.result-percentages{
    width: 30%;
    display: flex;
    flex-direction: column;
    background-color: #F3F1FE;

  span{
    font-weight: 800;
    font-size: 1.1rem;
    background-color: #F3F1FE;
    }

  small{
    background-color: #F3F1FE
    }
}

.system-section{
    display: flex;
    flex-direction: column;
    gap: 1rem;
    background-color: #fff;
    padding: 1.7rem;
    border-radius: 0 0 15px 15px;
}

.system-header{
    display: flex;
    background-color: #fff;

  h3{
    font-weight: 800;
    font-size: 1.1rem;
    text-transform: uppercase;
    background-color: #fff; 
    }
}

.system-names{
    display: flex;
    gap: 2rem;
    background-color: #fff;

  img{
    background-color: #fff;
    }
}

// SLIDER
.swiper-wrapper{
    padding-left: 35px;
}

.swiper-button-next::after,
.swiper-button-prev::after{
    color: #D1D1D1 ;
}


//FOOTER

.footer{
    width: 100%;
    display: flex;
    flex-direction: column;
    background-color: #fff;
}

.footer-top{
    justify-content: space-between;
    display: flex;
    padding: 3rem 5rem;
    background-color: #fff;
    flex-wrap: wrap;
    gap: 2rem;
}

.footer-logo-section{
    min-width: 45%;
    display: flex;
    flex-direction: column;
    gap: 1rem;
    background-color: #fff;
}

.footer-logo-pic{
    width: 19rem;
    display: flex;
    background-color: #fff;

    img {
      height: 100%;
      width: 100%;
    }
}

.footer-links{
   display: flex;
   padding-left: 1rem;
   background-color: #fff;
   
  a{
    font-size: 1rem;
    font-weight: 400;
    background-color: #fff;
    cursor: pointer;
   }

  a:hover{
    text-decoration-line: underline;
   }
}

.footer-button{
    display: flex;
    padding-left: 1rem;
    background-color: #fff;
    
  button {
    @include button
    }
}

.footer-links-section{
    min-width: 45%;
    display: flex;
    justify-content: space-between;
    background-color: #fff;
    gap: 1rem;
    padding: 2rem 0 0 1rem;
}

.information-services{
    display: flex;
    flex-direction: column;
    gap: .5rem;
    background-color: #fff;

  h1{
    font-weight: 600;
    font-size: 1rem;
    text-transform: uppercase;
    background-color: #fff;
    }

  a{
    font-weight: 400;
    font-size: 1rem;
    background-color: #fff;
    padding: 5px 10px;
    border-radius: 5px;
    cursor: pointer;
    }

  a:nth-child(even):hover {
    color: #fff;
    background-color: #F32C6D;
    }

  a:nth-child(odd):hover {
    color: #fff;
    background-color: #CB1D95;   
    }
}

.footer-bottom{
    width: 100%;
    display: flex;
    background-color: #2D2D2D;
    justify-content: center;
    align-items: center;
    padding: 10px 0;

  span{
    font-weight: 400;
    font-size: .8rem;
    color: #FFFFFF;
    background-color: #2D2D2D;
    }
}

.swal-button {
     background-color: #CB1D95;  
}

.swal-button:not([disabled]):hover {
    background-color: #F32C6D;
}


@media (max-width:900px) {
  .form-section{
    display: flex;
    flex-direction: column;
  }

  .form-section-left {
    width: 100%;
  }
     
  .form-section-right{
     width: 100%;
  }

}

 @media (max-width:500px) {
  .service-cards-section{
    display: flex;
    justify-content: center;
    }

  .card-service {
    width: 17rem ;
    }
    
  .card-service-button button {
    width: 17rem;
    }

  .name-phone-inputs {   
    display: flex;
    flex-direction: column;
    }      
         
  .user-name-input,
  .phone-number-input {
    width: 100%;
    
    input {
    width: 100%;   
    } 
    } 

  .our-work-card{
    width: 20rem;
    }

  .our-work-logo{
    p{
    align-self: center;
    text-align: center;    
    }  
    }

.result-bottom{
    display: flex;
    flex-direction: column;
    gap: .5rem;
   }

 .system-names{
    display: flex;
    flex-direction: column;
   }

 .padding-section {
    padding: 5rem 2rem;
   }

 .result-percentages {
    width: 100%;
   }
  
  .footer-top{
    padding: 1rem;
  }

  .footer{
    padding-bottom: 0;
   }

 }
 
 @media (max-width:400px) {
  .our-work-card{
    width: 12rem;
    }
  
  .button-section {
    button {
      padding: 3px 20px;
    }
  }

  .nav-links-expanded {
    a{
      padding: 0;
    }
  }
  
  

 }

 
// NAV-BAR-SECTION
 @media (max-width:768px) {
  .logo-section,
  .button-section {
    display: flex;
    align-self: flex-start;
    justify-content: center;
    padding: .5rem 0;
    background-color: #fff;
    }
    
  .nav-bar{
    gap: 1rem;
    }

  .nav-links{
    display: none;
    gap: .5rem;
    padding: 1rem 0;
    background-color: #fff;
    }

  .menu-icon{
    align-self: flex-start;
    display: flex;
    padding: 1.3rem 0;
    }
    
  .nav-links-expanded {
    display: flex;
    flex-direction: column;
    gap: .5rem;
    padding: 1rem 0;
    background-color: #fff;

   a{
    align-self: flex-start;
    background-color: #fff;
    }
 }

 .footer-bottom{
  padding: 1.5rem 0;
 }
 }